import React, {useState} from 'react'
import { Link } from "react-router-dom"
import './header.css'
import $ from 'jquery';
import {animateScroll as scroll} from "react-scroll";


window.onload = function(){
    $('.scroll-id').on('click', function(event){
      if(this.hash !==''){
        event.preventDefault();
        const hash = this.hash;
        $('html, body').animate(
          {
            scrollTop: $(hash).offset().top
          },
          800
        );
      }
    }); 
    }
    



function Header() {
    window.addEventListener("scroll", function(){
        var nav = document.getElementById('nav');
        nav.classList.toggle("nav_transform", window.scrollY>0);
        nav.classList.toggle("nav_ul_transform", window.scrollY>0);
        
      })

      const[scrollTop, setScrollTop] = useState();

      const scrollTopY = () =>{
          scroll.scrollToTop();
      }

    return (
        <div className='header'>
            <div className='header_flex'>
                <nav className='nav' id='nav'>
                    <div>
                    <ul>
                        <li><Link to="/"onClick={()=>setScrollTop(scrollTopY)}>Inicio</Link></li>
                        <li> <a href='#donde' className='scroll-id' >Donde y Cuando?</a></li>
                    </ul>
                    </div>
                    <div className='invitame'><h2>INVITAME!</h2></div>
                    <div>
                    <ul>
                        <li><a href='#confirmar-asistencia' className='scroll-id'>Confirmar asistencia</a></li>
                      {/* <li><a target="_blank" rel="noopener noreferrer" href="https://wa.me/526629364233?text=Confirmar asistencia%20con%20un%20asesor">Confirmar asistencia</a></li> */}
                        <li className='nosotros'><Link to="/nosotros"id='inicio'>Nosotros</Link></li>
                     </ul>
                    </div>
                </nav>
            </div>
        </div>
    )
}

export default Header